// === COLORS
$green: #27AE60;
$white: #fff;
$black: #212121;
$gray: #333333;
$gray2: #4F4F4F;

// END_COLORS
// === TRANSITION
$transition-function: ease-in-out;
$transition-duration: 200ms;
// === END_TRANSITION
$breakpoints: (
  xs: 480px,
  sm: 768px,
  md: 1000px,
);

@mixin desktop-container() {
  @include respond-above(md) {
    min-width: 1000px;
    max-width: 1277px;
  }
}


